<template>
  <div v-if="dialogLoginFormVisible">
    <div class="dialog_login">
      <el-dialog
        title=""
        :visible.sync="dialogLoginFormVisible"
        center
        @close="closeDialog"
        :modal="false"
        top="8vh"
      >
        <el-dialog width="25%" title="" :visible.sync="innerVisible" append-to-body>
          <el-form :model="forgetForm">
          <el-form-item>
            <el-input
              autocomplete="off"
              v-model="forgetForm.name"
              placeholder=""
              prefix-icon="el-icon-user"
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="forgetForm.phone"
              placeholder=""
              prefix-icon="el-icon-phone"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-input
              type="password"
              autocomplete="off"
              v-model="forgetForm.newPass"
              show-password
              placeholder=""
              prefix-icon="el-icon-key"
            ></el-input>
          </el-form-item>
        </el-form>
          <el-button @click="forgetPass()" type="primary" style="margin:0.1rem 1.1rem"></el-button>
        </el-dialog>
        <div class="toregister">
          
          <span @click="toRegister"></span>
        </div>
        <el-form :model="loginForm" ref="loginForm" :rules="rules">
          <el-form-item prop="name">
            <el-input
              autocomplete="off"
              v-model="loginForm.name"
              placeholder=""
              prefix-icon="el-icon-user"
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input
              type="password"
              autocomplete="off"
              v-model="loginForm.pass"
              show-password
              placeholder="6-12"
              prefix-icon="el-icon-lock"
            ></el-input>
          </el-form-item>
          <el-form-item prop="checkPass">
            <el-input
              type="password"
              autocomplete="off"
              v-model="loginForm.checkPass"
              show-password
              placeholder=""
              prefix-icon="el-icon-key"
            ></el-input>
          </el-form-item>
          <div class="el_captcha">
            <el-form-item prop="captcha">
              <el-input
                autocomplete="off"
                v-model="loginForm.captcha"
                placeholder=""
                prefix-icon="el-icon-full-screen"
                @keyup.enter.native="submitForm('loginForm')"
              ></el-input>
            </el-form-item>
            <span v-html="captcha" @click="refresh"></span>
          </div>
        </el-form>
        <span class="forgetPass" @click="innerVisible=true"></span>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitForm('loginForm')"></el-button>
          <div class="read">
             
            <router-link :to="{ name: 'Agreement', params: { city:this.$store.state.city}}">
              <span @click="close"></span>
            </router-link> 
            <router-link :to="{ name: 'Policy', params: { city:this.$store.state.city}}">
              <span @click="close"></span>
            </router-link>
          </div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>


<style>
.dialog_login .el-dialog {
  width: 30%;
}
.dialog_login .el-dialog__title {
  font-size: 0.28rem;
  font-weight: bold;
}

.dialog_login .el-form-item {
  margin-bottom: 0.3rem;
}
.dialog_login .el-dialog__body {
  padding: 0.15rem 0.4rem 0rem;
}
.dialog_login .el-form {
  margin-top: 0.2rem;
}
.dialog_login .el-dialog__close:hover {
  color: #ff9024 !important;
}
.dialog_login .toregister {
  font-size: 0.16rem;
}
.dialog_login .toregister span {
  color: #ff9024;
  cursor: pointer;
}
.dialog_login .el-button {
  background-color: #ff9024;
  width: 88%;
  color: white;
  font-size: 0.16rem;
  border: none;
}
.dialog_login .forgetPass {
  text-decoration: none;
  color: #ff9024;
  cursor: pointer;
}
.dialog_login .el-form-item:last-child {
  margin-bottom: 0.2rem;
}
.dialog_login .read {
  color: #999999;
  padding-top: 0.15rem;
  font-size: 0.12rem;
}
.dialog_login .read a {
  color: #6b91b3;
}
.dialog_login .el_captcha {
  display: flex;
  justify-content: space-between;
}
</style>

<script>
export default {
  data() {
    var checkName = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else {
        if (value.length < 6 || value.length > 12) {
          callback(new Error("6-12"));
        } else {
          if (this.loginForm.chenkPass !== "") {
            this.$refs.loginForm.validateField("checkPass");
          }
          callback();
        }
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value.length < 6 || value.length > 12) {
        callback(new Error("6-12"));
      } else if (value !== this.loginForm.pass) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    var validateCaptcha = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    return {
      innerVisible: false,
      captcha: "",
      dialogLoginFormVisible: false,
      dialogRegisterFormVisible: false,
      loginForm: {
        name: "",
        pass: "",
        checkPass: "",
        captcha: ""
      },
      forgetForm:{
        name:"",
        phone:"",
        newPass:""
      },
      rules: {
        name: [{ validator: checkName, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        captcha: [{ validator: validateCaptcha, trigger: "blur" }]
      }
    };
  },
  props: {
    show: Boolean
  },
  mounted() {
    if (!window.sessionStorage.getItem("token")) {
      this.refresh();
    }
  },
  methods: {
    refresh() {
      this.$http
        .get("/api/users/captcha")
        .then(response => {
          this.captcha = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    isShow() {
      this.dialogLoginFormVisible = !this.dialogLoginFormVisible;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http
            .post("/api/users/login", this.loginForm)
            .then(response => {
              //
              if (response.data.captcha === false) {
                this.$message({
                  type: "error",
                  message: ""
                });
              } else {
                //
                if (response.data.info === false) {
                  this.$message({
                    type: "info",
                    message: ""
                  });
                  return;
                }
                //
                if (response.data.success) {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.closeDialog();
                  this.checkSecurity();
                  this.$parent.newMessageNumber();
                  this.dialogLoginFormVisible = false;
                  //tokenusernamestore
                  let token = response.data.token;
                  let username = response.data.username;
                  this.$store.dispatch("UserLogin", token);
                  this.$store.dispatch("UserName", username);
                } else {
                  this.$message({
                    type: "error",
                    message: ""
                  });
                }
              }
            })
            .catch(function(error) {
              console.log(error);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    forgetPass(){
      this.$http.post('api/users/forgetPass',this.forgetForm).then(response=>{
        if(response.data.info=='success'){
          this.$message.success('')
          this.innerVisible=false
        }else if(response.data.info=='name'){
          this.$message.error('')
        }else if(response.data.info=='phone'){
          this.$message.error('')
        }
      })
    },
    close() {
      this.closeDialog();
      this.dialogLoginFormVisible = false;
    },
    closeDialog() {
      this.$emit("closeModal");
      this.$refs["loginForm"].resetFields();
    },
    toRegister() {
      this.dialogLoginFormVisible = false;
      this.$emit("openLogin");
    },
    checkSecurity() {
      this.$http.get("api/users/checkSecurity").then(response => {
        if (response.data.password_grade < 2) {
          this.util.newMessage(
            "",
            "",
            "token",
            "userprofile",
            1
          );
        }
      });
    }
  },
  watch: {
    dialogLoginFormVisible(n, o) {
      if (n) document.body.classList.add("el-popup-parent--hidden");
    }
  }
};
</script>


