<template>
  <div class="user_content">
    <div class="user_content_title">
      <p class="no_select_title"></p>
      <p class="select_title"></p>
    </div>
    <div class="no_user_msg" v-if="userHouseLenAuditFailed==0">
      <div class="no_msg_img"></div>
      <div class="no_msg_text">~</div>
    </div>
    <div v-else>
      <div class="info_table margin_top">
        <el-table :data="userHouseListAuditFailed" border style="width: 100%">
          <el-table-column prop="house_number" width="180" label=""></el-table-column>
          <el-table-column prop="rental_mode" width="120" label=""></el-table-column>
          <el-table-column prop="community_name" width="160" label=""></el-table-column>
          <el-table-column prop="price" label="" width="120"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
               <el-button size="mini" type="primary" @click="handleSubmit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="info" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
              <el-button
                size="mini"
                type="success"
                @click="handleReason(scope.$index, scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userHouseListAuditFailed: new Array(),
      userHouseLenAuditFailed: ""
    };
  },
  methods: {
    getUserHouseAuditFailed() {
      this.$http
        .get("api/users/getUserHouse", {
          params: { houseState: 3 }
        })
        .then(response => {
          this.userHouseListAuditFailed = response.data.userHouseList;
          this.userHouseLenAuditFailed = response.data.userHouseList.length;
        });
    },
    handleCheck(index, row) {
      this.$router.push({ name: "ViewHouse", params: { houseid: row._id } });
    },
    handleEdit(index, row) {
      this.$router.push({ name: "EditHouse", params: { houseid: row._id } });
    },
    handleDelete(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http
            .get("api/users/deleteUserHouse", {
              params: {
                houseId: row._id
              }
            })
            .then(response => {
              if (response.data.success == "true") {
                this.$message.success("");
                this.getUserHouseAuditFailed();
              } else {
                this.$message.warning("");
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handleReason(index, row) {
      this.$http
        .get("api/houses/checkFailedReason", {
          params: {
            houseId: row._id
          }
        })
        .then(response => {
          this.reason = response.data.reason;
          this.$alert(response.data.reason.reason, "", {
            confirmButtonText: "",
            callback: action => {
              this.$router.push({
                name: "EditHouse",
                params: { houseid: response.data.reason.house_id }
              });
            }
          });
        });
    },
     handleSubmit(index, row){
      this.$http.get('api/houses/resubmission',{
        params:{
          houseId:row._id
        }
      }).then(response=>{
        if(response.data.success=='true'){
          this.$message.success('')
           this.getUserHouseAuditFailed();
        }
      })
    }
  },
  mounted() {
    this.getUserHouseAuditFailed();
  }
};
</script>

<style>
.info_table .el-table {
  width: 96% !important;
  margin: 0 auto;
}
.margin_top .el-table {
  margin-top: 0.3rem !important;
}
</style>


