<template>
  <div class="user_content" v-if="!loading">
    <div class="user_content_title">
      <p class="no_select_title"></p>
      <p class="select_title"></p>
    </div>
    <div class="view_house">
      <ul>
        <li>
          <span></span>
          <span>{{houseDetail.house_number}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.housing_type}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.rental_mode}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.community_name}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.sq_value.district_id.district_name}}-{{houseDetail.sq_value.sq_name}}-{{houseDetail.cell_address}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.room}}{{houseDetail.hall}}{{houseDetail.toilet}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.floorage}}</span>
        </li>
        <li>
          <span></span>
          <span>
            {{houseDetail.floor}}{{houseDetail.floor_all}}
            <span
              v-if="houseDetail.elevator==true"
            ></span>
            <span v-else></span>
          </span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.house_orientation}}</span>
        </li>
        <li>
          <span></span>
          <span>
            <span
              v-if="houseDetail.subway_checked==true"
            >{{houseDetail.subway_station_value.subwayLine_id.subwayLine_name}}-{{houseDetail.subway_station_value.subwayStation_name}}</span>
            <span v-else></span>
          </span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.price}}/{{houseDetail.payment_method}}</span>
        </li>
        <li>
          <span></span>
          <span>
            <span v-for="(item,index) in houseDetail.rent_includes" :key="index">{{item}}</span>
          </span>
        </li>
        <li>
          <span></span>
          <span>{{this.$moment(houseDetail.check_in_time).format("YYYY-MM-DD")}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.number}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.renovation}}</span>
        </li>
        <li>
          <span></span>
          <span>
            <span v-for="(item,index) in houseDetail.configuration_includes" :key="index">{{item}}</span>
          </span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.rental_requirements}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.housing_description}}</span>
        </li>
        <li>
          <span></span>
          <span>
            <span v-for="(item,index) in houseImgUrl" :key="index">
              <img style="padding-left:0.1rem" :src="item" alt />
            </span>
          </span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.contacts}}{{houseDetail.contactssex}}</span>
        </li>
        <li>
          <span></span>
          <span>{{houseDetail.contact_number}}</span>
        </li>
        <li>
          <span></span>
          <span>{{this.$moment(houseDetail.create_time).format("YYYY-MM-DD")}}</span>
        </li>
        <li>
          <span></span>
          <span>{{this.$moment(houseDetail.update_time).format("YYYY-MM-DD")}}</span>
        </li>
                <li>
          <span></span>
          <span v-if="houseDetail.house_state==1"></span>
          <span v-else-if="houseDetail.house_state==2"></span>
          <span v-else-if="houseDetail.house_state==3"></span>
          <span v-else-if="houseDetail.house_state==4"></span>
        </li>
        <li>
          <span></span>
           <span v-if="houseDetail.promotion!='false'">
              <span style="padding-right:0.3rem" v-if="houseDetail.promotion=='true'"></span>
              <span style="padding-right:0.3rem" v-if="houseDetail.promotion=='wait'"></span>
              <span
                style="padding-right:0.3rem"
              >{{this.$moment(nowPromotion.start_time).format("YYYY-MM-DD")}}</span>
              <span>{{this.$moment(nowPromotion.end_time).format("YYYY-MM-DD")}}</span>
            </span>
             <span v-else>
              <router-link :to="{name:'PromotionHouse',params:{number:this.houseDetail.house_number}}" style="text-decoration:none;color:#ff9024;cursor:pointer;font-weight:bold;padding-left:0.3rem"></router-link>
            </span>
        </li>
         <li v-if="reason">
          <span></span>
          <span>
            {{reason.reason}}
          </span>          
        </li>
        <li>
          <div class="page_effects">
            <el-button type="primary" @click="toCheck()"></el-button>
          </div>
        </li>        
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      houseDetail: "",
      houseImgUrl: [],
      nowPromotion: "",
      reason:""
    };
  },
  methods: {
     getPromotion() {
      this.$http
        .get("api/houses/getPromotion", {
          params: {
            house_id: this.$route.params.houseid
          }
        })
        .then(response => {
          this.nowPromotion = response.data.nowPromotion;
        });
    },
    getHouseDetail() {
      this.loading = true;
      this.$http
        .get("api/houses/getHouseDetail", {
          params: {
            house_id: this.$route.params.houseid
          }
        })
        .then(response => {
          this.houseDetail = response.data.houseDetail[0];
          this.houseDetail.house_img = JSON.parse(this.houseDetail.house_img);
          this.houseImgUrl = Object.keys(this.houseDetail.house_img);
          this.title_img = this.houseImgUrl[0];
          if (this.houseDetail.promotion == 'true'||this.houseDetail.promotion == 'wait') {
            this.getPromotion();
              this.loading = false;
          } else {
            this.loading = false;
          }
          if (this.houseDetail.house_state == 3) {
            this.$http
              .get("api/houses/checkFailedReason", {
                params: {
                  houseId: this.houseDetail._id
                }
              })
              .then(response=>{
                this.reason=response.data.reason
              });
          }
        });
    },
    toCheck() {
      this.$router.push({ name: "Detail", params: { houseid: this.$route.params.houseid } });
    }
  },
  mounted() {
    this.getHouseDetail();
  }
};
</script>

<style scoped>
.view_house ul {
  list-style-type: none;
}
.view_house ul {
  padding-top: 0.2rem;
}
.view_house ul li {
  padding: 0.2rem 0 0.2rem 0.4rem;
}

.view_house > ul > li > span:nth-of-type(1) {
  color: rgb(131, 131, 131);
  display: inline-block;
  width: 10%;
}
.view_house > ul > li > span:nth-of-type(2) {
  display: inline-block;
  width: 88%;
  vertical-align: top;
}
.view_house img {
  width: 1.6rem;
  height: 1.6rem;
}
.page_effects {
  margin: 0 0 0.3rem 0;
}
</style>


