<template>
  <div class="user_content">
    <div class="user_content_title">
      <p class="no_select_title"></p>
      <p class="select_title"></p>
    </div>
    <div class="no_user_msg" v-if="userHouseLen==0">
      <div class="no_msg_img"></div>
      <div class="no_msg_text">~</div>
    </div>
    <div v-else>
      <div class="info_table">
        <p></p>
        <el-table :data="userHouseListDisplay" border>
          <el-table-column prop="house_number" width="180" label=""></el-table-column>
          <el-table-column prop="rental_mode" width="160" label=""></el-table-column>
          <el-table-column prop="community_name" width="160" label=""></el-table-column>
          <el-table-column prop="price" label="" width="160"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="handleCheck(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="info" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="info_table">
        <p></p>
        <el-table :data="userHouseListAudit" border style="width: 100%">
          <el-table-column prop="house_number" width="180" label=""></el-table-column>
          <el-table-column prop="rental_mode" width="160" label=""></el-table-column>
          <el-table-column prop="community_name" width="160" label=""></el-table-column>
          <el-table-column prop="price" label="" width="160"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="handleCheck(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="info" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="info_table">
        <p></p>
        <el-table :data="userHouseListAuditFailed" border style="width: 100%">
          <el-table-column prop="house_number" width="180" label=""></el-table-column>
          <el-table-column prop="rental_mode" width="130" label=""></el-table-column>
          <el-table-column prop="community_name" width="160" label=""></el-table-column>
          <el-table-column prop="price" label="" width="130"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="handleSubmit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="info" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="success" @click="handleReason(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="info_table">
        <p></p>
        <el-table :data="userHouseListDeleted" border style="margin-bottom:0.5rem">
          <el-table-column prop="house_number" width="180" label=""></el-table-column>
          <el-table-column prop="rental_mode" width="160" label=""></el-table-column>
          <el-table-column prop="community_name" width="160" label=""></el-table-column>
          <el-table-column prop="price" label="" width="160"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="handleCheck(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="info" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button
                size="mini"
                type="success"
                @click="handleRecovery(scope.$index, scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userHouseList: new Array(),
      userHouseLen: "",
      userHouseListDisplay: new Array(),
      userHouseListAudit: new Array(),
      userHouseListAuditFailed: new Array(),
      userHouseListDeleted: new Array(),
      reason:""
    };
  },
  methods: {
    getUserHouse() {
      this.$http.get("api/users/getUserHouse").then(response => {
        this.userHouseList = response.data.userHouseList;
        this.userHouseLen = response.data.userHouseList.length;
        for (var i = 0; i < this.userHouseLen; i++) {
          if (this.userHouseList[i].house_state == 1) {
            this.userHouseListDisplay.push(this.userHouseList[i]);
          } else if (this.userHouseList[i].house_state == 2) {
            this.userHouseListAudit.push(this.userHouseList[i]);
          } else if (this.userHouseList[i].house_state == 3) {
            this.userHouseListAuditFailed.push(this.userHouseList[i]);
          } else if (this.userHouseList[i].house_state == 4) {
            this.userHouseListDeleted.push(this.userHouseList[i]);
          }
        }
      });
    },
    handleCheck(index, row) {
      this.$router.push({ name: "ViewHouse", params: { houseid: row._id } });
    },
    handleEdit(index, row) {
      this.$router.push({ name: "EditHouse", params: { houseid: row._id } });
    },
    handleDelete(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http
            .get("api/users/deleteUserHouse", {
              params: {
                houseId: row._id
              }
            })
            .then(response => {
              if (response.data.success == "true") {
                this.$message.success("");
                this.userHouseListDisplay = [];
                this.userHouseListAudit = [];
                this.userHouseListAuditFailed = [];
                this.userHouseListDeleted = [];
                this.getUserHouse();
              } else {
                this.$message.warning("");
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handleRecovery(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http
            .get("api/users/recoveryUserHouse", {
              params: {
                houseId: row._id
              }
            })
            .then(response => {
              if (response.data.success == "true") {
                this.$message.success("");
                this.userHouseListDisplay = [];
                this.userHouseListAudit = [];
                this.userHouseListAuditFailed = [];
                this.userHouseListDeleted = [];
                this.getUserHouse();
              } else {
                this.$message.warning("");
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handleReason(index, row){
      this.$http.get('api/houses/checkFailedReason',{
        params:{
          houseId:row._id
        }
      }).then(response=>{       
        this.reason=response.data.reason
        this.$alert(response.data.reason.reason, '', {
          confirmButtonText: '',
          callback: action => {
            this.$router.push({name:'EditHouse',params:{houseid:response.data.reason.house_id}})
          }
        });
      })
    },
    handleSubmit(index, row){
      this.$http.get('api/houses/resubmission',{
        params:{
          houseId:row._id
        }
      }).then(response=>{
        if(response.data.success=='true'){
          this.$message.success('')
          this.getUserHouse();
        }
      })
    }
  },
  mounted() {
    this.getUserHouse();
  }
};
</script>

<style scoped>
.info_table > p {
  padding: 0.4rem 0.3rem;
  color: #fdac5b;
  font-weight: bold;
}
</style>

<style>
.info_table .el-table {
  width: 96% !important;
  margin: 0 auto;
}
</style>


